<template>
  <i
    class="pi pi-info-circle"
    @mouseenter="show"
    @mouseleave="hide"
  />
  <OverlayPanel ref="panel">
    <ul style="padding-left: 20px; margin: 0">
      <li v-for="error in props.errors">
        {{ error }}
      </li>
    </ul>
  </OverlayPanel>
</template>

<script setup lang="ts">
import OverlayPanel from 'primevue/overlaypanel';
import {ref} from 'vue';
const props = defineProps<{errors: string[]}>();
const panel = ref(null as null | InstanceType<typeof OverlayPanel>);

function show(e: MouseEvent) {
  panel.value?.show(e);
}

function hide() {
  panel.value?.hide();
}
</script>
