<script setup lang="tsx">
import Column, {ColumnSlots} from 'primevue/column';
import {DynamicGrid, DynamicGridProps} from './dynamic-grid';
import AppCard from '@/components/markup/AppCard.vue';
import {DataTableProps} from 'primevue/datatable';
import InputText from 'primevue/inputtext';
import {
  buildGridColumns,
  ColumnConfig,
  builidFilterMeta,
  getGlobalColumns,
  AppGridConfig,
} from './configuration';
import {FilterMatchMode} from 'primevue/api';

interface Props<T extends object> {
  config: AppGridConfig<T>;
}

const props = defineProps<Props<any>>();

function regroupColumns(
  columns: AppGridConfig<any>['columns'],
  labels: AppGridConfig<any>['labels'],
): ColumnConfig[] {
  const result = [] as ColumnConfig[];

  for (const key of Object.keys(columns)) {
    const column = columns[key];

    if (!column) {
      continue;
    }

    const config: ColumnConfig = {
      ...column,
      props: {
        ...column.props,
        header: labels[key],
        field: key,
      },
    };

    result.push(config);
  }

  return result;
}

const configs = regroupColumns(props.config.columns, props.config.labels);
const globalFilterFields = getGlobalColumns(configs);

const tablePropsDefaults: DataTableProps = {
  globalFilterFields,
  filterDisplay: 'row',
  stripedRows: true,
  paginator: !props.config.hidePaging,
  rows: 10,
  paginatorTemplate:
    'CurrentPageReport FirstPageLink PrevPageLink PageLinks NextPageLink LastPageLink RowsPerPageDropdown',
  rowsPerPageOptions: [10, 20, 50],
  currentPageReportTemplate:
    'Показаны записи с {first} по {last}. Всего: {totalRecords}',
  editMode: props.config.allowEdit ? 'row' : undefined,
};

const filtersMeta = builidFilterMeta(configs);

if (globalFilterFields.length) {
  filtersMeta['global'] = {value: null, matchMode: FilterMatchMode.CONTAINS};
}

const tableProps = Object.assign({}, tablePropsDefaults, props.config.props);
const filtersModel = ref(Object.assign({}, filtersMeta));
const editingRows = ref([]);
const loading = ref(false);
const expandedRows = ref([] as any);
const data = ref([] as any[]);
const globalProps = computed(
  () =>
    ({
      props: {
        expandedRows: expandedRows.value,
        editingRows: editingRows.value,
        filters: filtersModel.value,
        class: 'p-datatable-sm',
        ...tableProps,
        value: data.value,
        loading: loading.value,
      },
      emits: {
        'update:editingRows': (x: any) => {
          editingRows.value = x;
        },
        'update:filters': (x: any) => {
          filtersModel.value = x;
        },
        'update:expandedRows': (x: any) => {
          expandedRows.value = x;
        },
        ...props.config.emits,
      },
      slots: {
        default: () => [
          props.config.allowExpand ? (
            <Column expander style="width: 5rem" />
          ) : (
            ''
          ),
          buildGridColumns(configs),
          editColumn(
            tableProps.editMode === 'row',
            props.config.allowAdd,
            props.config.allowDelete,
          ),
        ],
        empty: () => 'Записи не найдены',
        ...props.config.slots,
      },
    } as DynamicGridProps),
);

function editColumn(
  allowEdit: boolean,
  allowAdd?: () => void,
  allowDelete?: (item: any) => void,
) {
  return (
    <Column
      row-editor={true}
      style="width: 100px"
      header-style="text-align:center"
      body-style="text-align:center"
    >
      {{
        header: () =>
          allowAdd ? (
            <div style="display: flex; justify-content: center; flex-grow: 1;">
              <button
                onClick={allowAdd}
                class="p-row-editor-init p-link"
                type="button"
                aria-label="Row Add"
                data-pc-section="roweditorinitbutton"
                data-pd-ripple="true"
              >
                <i class="pi pi-plus" />
              </button>
            </div>
          ) : (
            ''
          ),
        body: (scope => {
          return [
            allowEdit ? (
              <button
                onClick={() => scope.editorInitCallback()}
                class="p-row-editor-init p-link"
                type="button"
              >
                <i class="pi pi-pencil" />
              </button>
            ) : (
              ''
            ),
            allowDelete ? (
              <button
                onClick={() => allowDelete(scope.data)}
                class="p-row-editor-init p-link"
                type="button"
              >
                <i class="pi pi-trash" />
              </button>
            ) : (
              ''
            ),
          ];
        }) as ColumnSlots['body'],
        editor: allowEdit
          ? ((scope => {
              console.log(scope);
              return [
                <button
                  onClick={() => scope.editorSaveCallback()}
                  class="p-row-editor-init p-link"
                  type="button"
                >
                  <i class="pi pi-check" />
                </button>,
                <button
                  onClick={() => scope.editorCancelCallback()}
                  class="p-row-editor-init p-link"
                  type="button"
                >
                  <i class="pi pi-times" />
                </button>,
              ];
            }) as ColumnSlots['editor'])
          : undefined,
      }}
    </Column>
  );
}

async function refresh() {
  loading.value = true;
  try {
    data.value = await (props.config.getData ?? (() => []))();
  } catch (e) {
    console.error(e);
    data.value = [];
  }

  loading.value = false;
}

onMounted(refresh);
defineExpose({refresh});
</script>

<template>
  <AppCard :basis="props.config.basis" :title="props.config.title">
    <template #actions>
      <Button
        v-if="props.config.allowUpdate"
        style="margin-right: 5px;"
        label="Обновить"
        @click="props.config.allowUpdate"
      />
      <span class="p-input-icon-left">
        <i class="pi pi-search" />
        <InputText
          v-model="(filtersModel.global as any).value"
          placeholder="Поиск"
        />
      </span>
    </template>
    <DynamicGrid v-bind="globalProps" />
  </AppCard>
</template>

<style lang="scss">
.add-button {
  cursor: pointer;
  flex-grow: 1;
  text-align: center;
}
</style>
