import Column, {ColumnSlots, ColumnProps} from 'primevue/column';
import {DataTableFilterMeta} from 'primevue/datatable';
import {
  DataTableProps,
  DataTableSlots,
  DataTableEmits,
} from 'primevue/datatable';

export interface ColumnConfig {
  props: ColumnProps;
  slots?: Partial<ColumnSlots>;
  globalFilter?: boolean;
  matchMode?: string;
}

export function buildGridColumn(config: ColumnConfig) {
  return <Column {...config.props}>{{...config.slots}}</Column>;
}

export function buildGridColumns(configs: ColumnConfig[]): JSX.Element {
  const array = configs.map(buildGridColumn);

  return <>{...array}</>;
}

export function builidFilterMeta(configs: ColumnConfig[]): DataTableFilterMeta {
  const result = {} as DataTableFilterMeta;

  for (const config of configs) {
    const matchMode = config.matchMode;

    if (matchMode) {
      result[String(config.props.field)] = {value: null, matchMode};
    }
  }

  return result;
}

export function getGlobalColumns(configs: ColumnConfig[]) {
  const result = [] as string[];

  for (const config of configs) {
    if (config.globalFilter) {
      result.push(String(config.props.field));
    }
  }

  return result;
}

export interface AppGridConfig<T extends object> {
  basis?: string;
  title?: string;
  props?: Partial<DataTableProps>;
  slots?: Partial<DataTableSlots>;
  emits?: Partial<DataTableEmits>;
  getData?: () => Promise<T[]>;
  hidePaging?: boolean;
  allowUpdate?: () => void;
  allowAdd?: () => void;
  allowDelete?: (item: T) => void;
  allowEdit?: boolean;
  allowExpand?: boolean;
  columns: Record<keyof T, ColumnConfig | false>;
  labels: Record<keyof T, string>;
}

export interface AppFormGridConfig<T> {
  basis?: string;
  title?: string;
  allowUpdate?: () => void;
  allowAdd: boolean;
  allowEdit: boolean;
  allowDelete: boolean;
  onDeleted?: (item: T) => void;
  dataKey: string;
  newItemFunc?: (currentLength: number) => Partial<T>;
  validationFunc?: (value: Partial<T>) => string[];
  labels: Record<keyof T, string>;
  columns: Record<keyof T, false | ColumnConfig>;
}

export interface PartialAppGridConfig<T extends object> {
  title: string;
  props?: Partial<DataTableProps>;
  slots?: Partial<DataTableSlots>;
  emits?: Partial<DataTableEmits>;
  getData?: () => Promise<T[]>;
  hidePaging?: boolean;
  allowAdd?: boolean | Function;
  allowEdit?: boolean;
  allowExpand?: boolean;
  columns?: Partial<Record<keyof T, ColumnConfig | false>>;
  labels?: Partial<Record<keyof T, string>>;
}
