import {defineComponent} from 'vue';
import {
  default as DataTable,
  DataTableProps,
  DataTableSlots,
  DataTableEmits,
} from 'primevue/datatable';

export interface DynamicGridProps {
  props: DataTableProps;
  slots: DataTableSlots;
  emits: DataTableEmits;
}

export const DynamicGrid = defineComponent({
  name: 'DynamicGrid',
  props: ['props', 'slots', 'emits'],
  render: (props: DynamicGridProps) => {
    return h(
      DataTable,
      {
        ...props.props,
        ...emitsToProps(props.emits),
      },
      {...props.slots},
    );
  },
});

function emitsToProps(emits: Record<string, any>) {
  const props = {} as Record<string, any>;

  for (const key of Object.keys(emits)) {
    props['on' + snakeToPascal(key)] = emits[key];
  }

  return props;
}

function snakeToPascal(s: string) {
  return s.replace(/(^\w)|([-_]\w)/g, g => (g[1] ?? g[0]).toUpperCase());
}
