import {ColumnSlots} from 'primevue/column';
import InputNumber from 'primevue/inputnumber';
import InputText from 'primevue/inputtext';
import Dropdown from 'primevue/dropdown';
import TriStateCheckbox from 'primevue/tristatecheckbox';
import Checkbox from 'primevue/checkbox';
import Tag from 'primevue/tag';
import {ColumnConfig} from './configuration';
import {FilterMatchMode} from 'primevue/api';
import {RouterLink} from 'vue-router';
import {DateTime} from 'luxon';
import AppErrorIcon from './app-error-icon.vue';

export const plainGridFilter: ColumnSlots['filter'] = ({
  filterModel,
  filterCallback,
}) => {
  return [
    h(InputText, {
      modelValue: filterModel.value,
      'onUpdate:modelValue': (value: string) => {
        filterModel.value = value;
        filterCallback();
      },
    }),
  ];
};

export function severityGridFilter(
  map: Record<string, SeverityItem>,
): ColumnSlots['filter'] {
  return ({filterModel, filterCallback}) => {
    return [
      h(
        Dropdown,
        {
          modelValue: filterModel.value,
          'onUpdate:modelValue': (value: string) => {
            filterModel.value = value;
            filterCallback();
          },
          options: Object.keys(map),
        },
        {
          option: (slotProps: any) => (
            <Tag
              value={map[slotProps.option]?.label}
              severity={map[slotProps.option]?.severity}
            />
          ),
          value: ({value}: any) =>
            value
              ? [
                  <Tag
                    value={map[value]?.label}
                    severity={map[value]?.severity}
                  />,
                ]
              : [],
        },
      ),
    ];
  };
}

export const plainGridEditor: ColumnSlots['editor'] = ({data, field}) => {
  return [
    h(InputText, {
      modelValue: data[field],
      'onUpdate:modelValue': (value: string) => {
        data[field] = value;
      },
    }),
  ];
};

export const integerGridEditor: ColumnSlots['editor'] = ({data, field}) => {
  return [
    h(InputNumber, {
      modelValue: data[field],
      'onUpdate:modelValue': (value: string) => {
        data[field] = value;
      },
    }),
  ];
};

export const currencyGridEditor: ColumnSlots['editor'] = ({data, field}) => {
  return [
    h(InputNumber, {
      modelValue: data[field],
      'onUpdate:modelValue': (value: string) => {
        data[field] = value;
      },
      locale: 'en',
      minFractionDigits: 2,
      maxFractionDigits: 2,
    }),
  ];
};

export function severityGridEditor(
  map: Record<string, SeverityItem>,
): ColumnSlots['editor'] {
  return ({data, field}) => {
    return [
      h(
        Dropdown,
        {
          modelValue: data[field],
          'onUpdate:modelValue': (value: string) => {
            data[field] = value;
          },
          options: Object.keys(map),
        },
        {
          option: (slotProps: any) => (
            <Tag
              value={map[slotProps.option]?.label}
              severity={map[slotProps.option]?.severity}
            />
          ),
          value: ({value}: any) => {
            return value
              ? [
                  <Tag
                    value={map[value]?.label}
                    severity={map[value]?.severity}
                  />,
                ]
              : [];
          },
        },
      ),
    ];
  };
}

export function severityGridBody(
  map: Record<string, SeverityItem>,
): ColumnSlots['body'] {
  return ({data, field}) => {
    return [
      <Tag
        value={map[data[field]].label}
        severity={map[data[field]].severity}
      />,
    ];
  };
}

export const boolGridFilter: ColumnSlots['filter'] = ({
  filterModel,
  filterCallback,
}) => {
  return [
    h(TriStateCheckbox, {
      modelValue: filterModel.value,
      'onUpdate:modelValue': (value: string) => {
        filterModel.value = value;
        filterCallback();
      },
    }),
  ];
};

export const defaultPreset: ColumnConfig = {
  globalFilter: true,
  matchMode: FilterMatchMode.CONTAINS,
  props: {
    showFilterMenu: false,
    showClearButton: false,
    sortable: true,
  },
};

export const stringColumnPresetNoEdit: ColumnConfig = {
  ...defaultPreset,
  slots: {
    filter: plainGridFilter,
  },
};

export const currencyColumnPresetNoEdit: ColumnConfig = {
  ...defaultPreset,
  slots: {
    filter: plainGridFilter,
    body: ({data, field}) => {
      if (Number.isFinite(data[field])) {
        const result = Number(data[field]).toFixed(2);

        return [<>{result}</>];
      }

      return [];
    },
  },
};

export const currencyColumnPreset: ColumnConfig = {
  ...currencyColumnPresetNoEdit,
  slots: {
    ...currencyColumnPresetNoEdit.slots,
    editor: currencyGridEditor,
  },
};

export function validationOutcomePreset(
  validationFunc?: (value: any) => string[],
): ColumnConfig {
  return {
    ...defaultPreset,
    props: {
      sortable: false,
    },
    slots: {
      filter: plainGridFilter,
      body: ({data}) => {
        if (!validationFunc) {
          return [];
        }

        const result = validationFunc(data);

        if (result.length === 0) {
          return [];
        }

        return [<AppErrorIcon errors={result} />];
      },
    },
  };
}

export const dateTimeColumnPresetNoEdit: ColumnConfig = {
  ...defaultPreset,
  slots: {
    body: ({data, field}) => {
      const result = DateTime.fromISO(data[field], {zone: 'utc'})
        .toLocal()
        .toFormat('yyyy-MM-dd hh:mm:ss', {});

      return [<>{result}</>];
    },
  },
};

export const stringColumnPreset: ColumnConfig = {
  ...defaultPreset,
  slots: {
    editor: plainGridEditor,
    filter: plainGridFilter,
  },
};

export const integerColumnPreset: ColumnConfig = {
  ...defaultPreset,
  slots: {
    editor: integerGridEditor,
    filter: plainGridFilter,
  },
};

export interface SeverityItem {
  value: string | number;
  label: string;
  severity: string;
}

export function severityColumnPreset(items: SeverityItem[]): ColumnConfig {
  const map = {} as Record<string, SeverityItem>;

  for (const item of items) {
    map[item.value] = item;
  }

  return {
    ...defaultPreset,
    props: {
      ...defaultPreset.props,
      showClearButton: true,
    },
    slots: {
      filter: severityGridFilter(map),
      editor: severityGridEditor(map),
      body: severityGridBody(map),
    },
  };
}

export function routerLinkIdPreset(routerLinkPrefix: string): ColumnConfig {
  return {
    ...defaultPreset,
    slots: {
      filter: plainGridFilter,
      body: ({data, field}) => {
        const id = data[field];
        return id
          ? [
              <RouterLink
                style="text-decoration: underline;"
                to={routerLinkPrefix + id}
              >
                <div>{id}</div>
              </RouterLink>,
            ]
          : [];
      },
    },
  };
}

export const boolColumnPreset: ColumnConfig = {
  ...defaultPreset,
  slots: {
    editor: ({data, field}) => {
      return [
        h(Checkbox, {
          binary: true,
          modelValue: data[field],
          'onUpdate:modelValue': (value: string) => {
            data[field] = value;
          },
        }),
      ];
    },
    body: ({data, field}) => {
      const value = !!data[field];

      return [
        <i
          class={{
            pi: true,
            'pi-check-circle text-green-500': value,
            'pi-times-circle text-red-400': !value,
          }}
        />,
      ];
    },
    filter: boolGridFilter,
  },
};
